import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  Paper,
  useMediaQuery,
  Typography,
} from "@material-ui/core";
import React from "react";
import QuillEditor from "../../components/QuillEditor";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import ExamsBasicForm from "./ExamsBasicForm"
const useStyles = makeStyles((theme) => ({
  table: {
    minWidth: 650,
    // maxWidth: 1050,
    margin: "10px,10px",
  },
  TableCell: {
    fontWeight: "bold",
    backgroundColor: theme.palette.primary.dark,
    color: theme.palette.getContrastText(theme.palette.error.dark),
  },
}));
const ExamsForm = (props) => {
  const { handleClickOpen, open, handleClose } = props;
  const theme = useTheme();
  const classes = useStyles();


  return (
    <>
      <Button variant="contained" color="primary" onClick={handleClickOpen}>
        New Exam
      </Button>
      <Dialog
        fullWidth={true}
        maxWidth="lg"
        onClose={handleClose}
        aria-labelledby="customized-dialog-title"
        open={open}
      >
        <DialogTitle
          className={classes.TableCell}
          id="customized-dialog-title"
          onClose={handleClose}
        >
          Exam
        </DialogTitle>
        <DialogContent className={classes.table} dividers>
          <ExamsBasicForm handleClose={handleClose} />
        </DialogContent>
      
      </Dialog>
    </>
  );
};

export default ExamsForm;
