import React, { useState,useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import {
  Box,
  Breadcrumbs,
  Button,
  Container,
  Grid,
  TablePagination,
  Typography,
  Link,
  TableFooter,
} from "@material-ui/core";
import { Helmet } from "react-helmet-async";
import { Link as RouterLink } from "react-router-dom";
import useSettings from "../../hooks/useSettings";
import ChevronRightIcon from "../../icons/ChevronRight";
import UploadIcon from "../../icons/Upload";
import PlusIcon from "../../icons/Plus";
import DownloadIcon from "../../icons/Download";
import ExamsForm from "./ExamsForm";
import axios from "axios";
import {customAxios} from "../apis/Axios";
const useStyles = makeStyles((theme) => ({
  table: {
    // minWidth: 650,
  },
  TableContainer: {
    borderRadius: 15,
    margin: "10px ,10px",
    // maxWidth: 1050,
  },
  TableCell: {
    fontWeight: "bold",
    backgroundColor: theme.palette.primary.dark,
    color: theme.palette.getContrastText(theme.palette.error.dark),
  },
  // highlight:
  //     theme.palette.type === "light"
  //       ? {
  //           color: theme.palette.secondary.main,
  //           backgroundColor: lighten(theme.palette.secondary.light, 0.85),
  //         }
  //       : {
  //          backgroundColor: theme.palette.primary.dark,
  //     color: theme.palette.getContrastText(theme.palette.error.dark),
  //         },
}));


export default function ExamsList() {
  const classes = useStyles();
  const [open, setOpen] = useState(false);
  const [examData, setExams] = useState([]);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [page, setPage] = useState(0);
  const { settings } = useSettings();
  const [refresh,setRefresh]=useState(false)
  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    setRefresh(true);
  };
  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  useEffect(async () => {
    const Data = await customAxios.getCall("exams/get-exams");
    setExams(Data);
  }, [refresh]);
  return (
    <>
      <Helmet>
        <title>Dashboard: Invoice List | Material Kit Pro</title>
      </Helmet>
      <Box
        sx={{
          backgroundColor: "background.default",
          minHeight: "100%",
          py: 1,
        }}
      >
        <Container maxWidth={settings.compact ? "xl" : false}>
          <Grid container justifyContent="space-between" spacing={3}>
            <Grid item>
              <Typography color="textPrimary" variant="h5">
                Exam List
              </Typography>
              <Breadcrumbs
                aria-label="breadcrumb"
                separator={<ChevronRightIcon fontSize="small" />}
                sx={{ mt: 1 }}
              >
                <Link
                  color="textPrimary"
                  component={RouterLink}
                  to="/dashboard"
                  variant="subtitle2"
                >
                  Dashboard
                </Link>

                <Typography color="textSecondary" variant="subtitle2">
                  Exam List
                </Typography>
              </Breadcrumbs>
              <Box
                sx={{
                  mb: -1,
                  mx: -1,
                  mt: 1,
                }}
              >
                <Button
                  color="primary"
                  startIcon={<UploadIcon fontSize="small" />}
                  sx={{ m: 1 }}
                  variant="text"
                >
                  Import
                </Button>
                <Button
                  color="primary"
                  startIcon={<DownloadIcon fontSize="small" />}
                  sx={{ m: 1 }}
                  variant="text"
                >
                  Export
                </Button>
              </Box>
            </Grid>
            <Grid item>
              <Box sx={{ m: -1,mt:3 }}>
                <Button
                  color="primary"
                  startIcon={<PlusIcon fontSize="small" />}
                  sx={{ m: 1 }}
                  variant="contained"
                  onClick={handleClickOpen}
                >
                  New Exams
                </Button>
              </Box>
            </Grid>
          </Grid>
          <TableContainer component={Paper}>
            <Table className={classes.table} aria-label="caption table">
              <TableHead className={classes.TableCell}>
                <TableRow>
                  <TableCell>Name</TableCell>
                  <TableCell align="right">Session</TableCell>
                  <TableCell align="right">Subject Icluded</TableCell>
                  {/* <TableCell align="right">Status</TableCell> */}
                  <TableCell align="right">Publish Result</TableCell>
                  <TableCell align="right">Class (section)</TableCell>
                  <TableCell align="right">description</TableCell>
                  <TableCell align="right">Action</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {examData
                  .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                  .map((row) => (
                    <TableRow key={row.id}>
                      <TableCell component="th" scope="row">
                        {row.name}
                      </TableCell>
                      <TableCell align="right">{row.session}</TableCell>
                      <TableCell align="center">0</TableCell>
                      {/* <TableCell align="center">{row.status}</TableCell> */}
                      <TableCell align="center">{row.publish}</TableCell>
                      <TableCell align="center">{row.className}</TableCell>
                      <TableCell align="right">{row.description}</TableCell>
                      <TableCell align="right">
                        <ExamsForm
                          open={open}
                          handleClose={handleClose}
                          handleClickOpen={handleClickOpen}
                        />
                      </TableCell>
                    </TableRow>
                  ))}
              </TableBody>
              <TableFooter>
                <TablePagination
                  rowsPerPageOptions={[5, 10, 25]}
                  count={examData.length}
                  rowsPerPage={rowsPerPage}
                  page={page}
                  onPageChange={handleChangePage}
                  onRowsPerPageChange={handleChangeRowsPerPage}
                />
              </TableFooter>
            </Table>
          </TableContainer>
        </Container>
      </Box>
    </>
  );
}
