import React, { useState,useEffect } from "react";
import * as Yup from "yup";
import axios from "axios";
import {
  Button,
  DialogActions,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  TextField,
  RadioGroup,
  FormControlLabel,
  Radio,
  Box,
} from "@material-ui/core";
import { Formik } from "formik";
import { makeStyles,} from "@material-ui/core/styles";
import toast from "react-hot-toast";
import {customAxios} from "../apis/Axios";
const Session = [
  {
    value: "0",
    label: "2020-2021",
  },
  {
    value: "1",
    label: "2021-2022",
  },
  {
    value: "2",
    label: "2022-2023",
  },{
    value: "3",
    label: "2023-2024",
  },
];
const useStyles = makeStyles((theme) => ({
  table: {
    minWidth: 650,
    maxWidth: 1050,
    margin: "10px,10px",
  },
}));
const ExamsBasicForm = ({ handleClose }) => {
  const classes = useStyles();
const [description, setdescription] = useState("");
const [ExamGroup,setExamGroup]=useState([])
const [classObj, setClassObj] = useState([]);
const ExamsGruops=async()=>{
  const data = await customAxios.getCall("exams/examsgroup");
  setExamGroup(data)
}

 const classData = async () => {
   const data = await customAxios.getCall("class/api");
   setClassObj(data);
 };
useEffect(() => {
  ExamsGruops();
  classData();
}, [])

const handleChanges = (e) =>{
  console.log(e.taret.value);
  setdescription(e.taret.value);
}
  return (
    <>
      <Formik
        initialValues={{
          examsGroup: "",
          examName: "",
          classid: "",
          yearid: "",
          picked: "",
          description: "",
        }}
        validationSchema={Yup.object().shape({
          examName: Yup.string().required("Name is required"),
        })}
        onSubmit={async (values) => {
          alert(JSON.stringify(values, null));
          try {
            await customAxios
              .postCall("exams/api/createExams", {
                ...values,
              })
              .then((res) => {
                toast.success("congratulation");
                handleClose();
              });
          } catch (e) {
            console.log(e);
          }
        }}
      >
        {({
          errors,
          handleBlur,
          handleChange,
          handleSubmit,
          isSubmitting,
          touched,
          values,
        }) => (
          <form noValidate onSubmit={handleSubmit}>
            <div>
              <FormControl style={{ margin: 8, width: "30%" }}>
                <InputLabel id="examsGroup">Exams Group</InputLabel>

                <Select
                  autoFocus
                  fullWidth
                  labelId="examsGroup"
                  value={values.examsGroup}
                  onChange={handleChange}
                  id="examsGroup"
                  label="examsGroup"
                  name="examsGroup"
                  // multiple
                >
                  {ExamGroup.map((option) => (
                    <MenuItem key={option.value} value={option.id}>
                      {option.name}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
              <FormControl style={{ margin: 8, width: "30%" }}>
                <InputLabel id="Class">Class/Department</InputLabel>

                <Select
                  fullWidth
                  labelId="Class"
                  value={values.classid}
                  onChange={handleChange}
                  id="classid"
                  label="Class/Department"
                  name="classid"
                  // multiple
                >
                  {classObj.map((option) => (
                    <MenuItem key={option.value} value={option.id}>
                      {option.className}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
              <TextField
                autoComplete="off"
                id="examName"
                label="Exam Name"
                style={{ margin: 8, width: "30%" }}
                variant="outlined"
                type="text"
                error={Boolean(touched.examName && errors.examName)}
                helperText={touched.examName && errors.examName}
                onBlur={handleBlur}
                onChange={handleChange}
                value={values.examName}
              />
              <FormControl style={{ margin: 8, width: "30%" }}>
                <InputLabel id="yearid">Year</InputLabel>

                <Select
                  fullWidth
                  labelId="yearid"
                  value={values.yearid}
                  onChange={handleChange}
                  id="yearid"
                  label="Year"
                  name="yearid"
                  // multiple
                >
                  {Session.map((option) => (
                    <MenuItem key={option.value} value={option.label}>
                      {option.label}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </div>
            <FormControl style={{ paddingLeft: 10 }} component="fieldset">
              <RadioGroup
                row
                aria-label="picked"
                name="picked"
                id="picked"
                value={values.picked}
                onChange={handleChange}
              >
                <FormControlLabel
                  value="1"
                  control={<Radio />}
                  label="Publish"
                />
              </RadioGroup>
            </FormControl>

            <Box sx={{ mt: 3 }}>
              <TextField
                FormHelperTextProps={{
                  sx: {
                    textAlign: "right",
                    mr: 0,
                  },
                }}
                fullWidth
                helperText={`${200 - description.length} characters left`}
                label="Short Note"
                multiline
                name="description"
                id="dexription"
                onChange={handleChange}
                placeholder="What excites you about this project?"
                rows={15}
                value={values.description}
                variant="outlined"
              />
            </Box>
            <DialogActions>
              <Button onClick={handleClose} color="primary">
                Cancel
              </Button>
              <Button color="primary" disabled={isSubmitting} type="submit">
                Save
              </Button>
            </DialogActions>
          </form>
        )}
      </Formik>
    </>
  );
};
export default ExamsBasicForm